var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"info base_width"},[_c('h3',{staticClass:"header font-weight-normal"},[_vm._v("许家印40.7亿领衔慈善榜 恒大602亿领跑纳税榜")]),_c('h5',{staticClass:"tips font-weight-normal"},[_vm._v(" 发布时间：2019-04-16 内容来源：新华网")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"box-header"},[_c('p',{staticClass:"indent"},[_vm._v("4月16日，2019中国慈善企业家榜发布，许家印、马化腾、杨国强家族、李彦宏/马东敏夫妇、黄朝阳、陈发树、龚俊龙、卢志强、王文学、吴远溪/吴远港兄弟位列榜单前十。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({width: '480px',height: '2700px',backgroundImage: ("url(" + (require('../../assets/images/news/company_info61.png')) + ")")})})]),_c('p',{staticClass:"indent"},[_vm._v("据榜单，2019年100位入榜企业家（企业）的捐赠总额高达163.21亿元，与去年基本持平，其中42位企业家的捐赠超过1亿元。上榜门槛为1000万元。")]),_c('p',{staticClass:"indent"},[_vm._v("其中，恒大集团董事局主席许家印以40.70亿元捐赠额位居榜首。据统计，许家印领导的恒大集团从2015年12月开始结对帮扶贵州省毕节市，无偿投入110亿元，截至2018年年底累计已捐赠到位60亿元，帮扶52.13万人初步脱贫。同时，恒大集团还在2018年广东扶贫济困日捐款5亿元，用于帮扶广东省的扶贫工作。在教育方面，2018年恒大集团对中山大学、武汉科技大学等有大额捐赠。")]),_c('p',{staticClass:"indent"},[_vm._v("事实上，许家印一直被誉为积极承担社会责任的典范，其创立恒大以来，持续承担社会责任，累计为民生、扶贫、教育、环保、体育等慈善公益事业捐款100余次超120亿，为积累社会财富、创造就业岗位、促进经济社会发展做出了应有贡献。先后被授予全国脱贫攻坚奖、中国最佳企业公民、全国爱心捐助奖等多项荣誉，并连续八届荣膺中国慈善领域最高政府奖“中华慈善奖”。")]),_c('p',{staticClass:"indent"},[_vm._v("发布慈善企业家榜以来，三年来共计有226位企业家上榜，有24位企业家三年连续上榜。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({width: '404px',height: '1853px',backgroundImage: ("url(" + (require('../../assets/images/news/company_info62.png')) + ")")})})]),_c('p',{staticClass:"indent"},[_vm._v("而就企业而言，纳税越多意味着其创造越多的社会财富，也承担了更多的社会责任，因此，纳税额的多少也是一份企业的“慈善榜”。")]),_c('p',{staticClass:"indent"},[_vm._v("中国恒大凭借602.18亿的所得税排名第一，成为内房股的所得税“纳税王”，日均纳税2.6亿；万科、碧桂园所得税分别为377.93亿和310.21亿，位列第二和第三；华润置地、中国海外发展、龙湖集团、融创中国、雅居乐集团、世茂房地产和富力集团，分别排第四至第十名。")]),_c('p',{staticClass:"indent"},[_vm._v("值得一提的是，在中国纳税额超过300亿的企业屈指可数。2018年，恒大、万科和碧桂园等